import { getCurrencySymbol } from '@angular/common';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalController, ToastController } from '@ionic/angular';
import { format } from "date-fns"
import * as moment from 'moment';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from "src/app/services/data.service";
import { ChangeDetectorRef } from '@angular/core';
import { DatePipe } from '@angular/common';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { LoadingService } from 'src/app/services/loading.service';
import { AppointmentpopupPage } from '../appointmentpopup/appointmentpopup.page';

@Component({
  selector: 'app-chequewithdrawal',
  templateUrl: './chequewithdrawal.page.html',
  styleUrls: ['./chequewithdrawal.page.scss'],
})
export class ChequewithdrawalPage implements OnInit {
  title: any = 'Cheque Withdrawal';
  slideOneForm: FormGroup;
  customerId: string;
  public currentBalance: any;
  phoneNumber: string;
  curr: string;
  currencyValues: any;
  currencyData: any;
  currencies: any = [];
  customerDetails: any;
  chequeWithdrawal: any;
  transactionId: any;
  IntValue: number;
  nearestBrn: boolean;
  accBranch: string;
  accountInfo: any;
  tokenCount: any;
  accountDetails: any;
  productCode = 'CQW';
  tokenOrigin = 'Mobile';
  // for transaction amount comma separator
  //transactionAmount="10,000";
  //transactionAmount:double;
  transactionAmount: any;
  transAmount: string;
  //transAmount:number;
  isedit: boolean = true;
  transAmt: any;

  accountBranch = "Loita street";
  flag: boolean = true;
  currencyValue: string;
  cashWithdrawResponse: any;
  submitted: boolean = true;
  submitted1: boolean = true;
  users: any[];
  accountNum: string;
  transDate: string
  transTime: string;
  trnBrn = null;
  brnflg: any;
  minDate = new Date().toISOString();
  maxDate: any = "2025";
  routerData: any;
  accountflag: boolean = false;
  exchangeRateInfo: any;
  chargeAmount: any;
  editMode: boolean = false;
  isShow: boolean = true;
  selectedCountryCode = '';
  selectedCurrency: any;

  constructor(private router: Router,
    private fb: FormBuilder,
    private api: ApiService, public toastCtrl: ToastController,
    private modalController: ModalController,
    public datepipe: DatePipe,
    public loading: LoadingService,
    private shareDataService: DataService, private cdr: ChangeDetectorRef) { }

  ngOnInit() {

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      console.log("Values?", this.routerData);
      this.editMode = this.routerData?.data ? true : false;
    }

    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
    }

    this.buildForm();
    this.loadData();

    this.slideOneForm.get('branchFlag').valueChanges.subscribe(val => {
      localStorage.setItem("BranchFlag", val);
      this.brnflg = val;

      if (this.brnflg == false && val == false) {
        this.slideOneForm.controls.transactionBranch.patchValue(this.trnBrn);
        this.nearestBrn = true;
      } else {
        this.nearestBrn = false;
        this.accBranch = localStorage.getItem("AccBranch");
        console.log(this.accBranch);
        this.slideOneForm.controls.transactionBranch.patchValue(this.accBranch);
      }
      if (this.brnflg == true && val == false) {
        console.log(this.accBranch);
        this.slideOneForm.controls.transactionBranch.patchValue(this.accBranch);
        this.nearestBrn = true;
      }
    });

    this.slideOneForm.get('accountNumber').valueChanges.subscribe(val => {
      if (val) {
        this.accountEvent(val);
      }
    });


    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
      console.log(data);
      this.api.getNumberOfCrowd(this.accountInfo.accountBranch)
        .subscribe((data1: any) => {
          this.tokenCount = data1.tokenCount || '0';
        })
    });

    let today = new Date().toISOString();
    this.slideOneForm.get('transactionDate').patchValue(today);
  }

  getCurrencyFormat(val: number) {
    if (val) {
      let data = val.toLocaleString('en-US');
      console.log(data);
      return data;
    }
  }

  numberWithCommas(amount: any) {
    if (amount) {
      const formattedNumber = amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      return formattedNumber;
    }
  }

  // Only Integer Numbers
  keyPressNumbers(event) {
    var charCode = (event.which) ? event.which : event.keyCode;
    // Only Numbers 0-9
    if ((charCode < 48 || charCode > 57)) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
    }

    this.buildForm();
    this.loadData();

    this.slideOneForm.get('branchFlag').valueChanges.subscribe(val => {
      localStorage.setItem("BranchFlag", val);
      this.brnflg = val;
      if (this.brnflg == false && val == false) {
        this.slideOneForm.controls.transactionBranch.patchValue(this.trnBrn);
        this.nearestBrn = true;
      } else {
        this.nearestBrn = false;
        this.accBranch = localStorage.getItem("AccBranch");
        console.log(this.accBranch);
        this.slideOneForm.controls.transactionBranch.patchValue(this.accBranch);
      }
      if (this.brnflg == true && val == false) {
        console.log(this.accBranch);
        this.slideOneForm.controls.transactionBranch.patchValue(this.accBranch);
        this.nearestBrn = true;
      }
    });

    this.slideOneForm.get('accountNumber').valueChanges.subscribe(val => {
      if (val) {
        this.accountEvent(val);
      }
    });

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
      this.api.getNumberOfCrowd(this.accountInfo.accountBranch)
        .subscribe((data1: any) => {
          this.tokenCount = data1.tokenCount || '0';
        })
    });
  }

  buildForm() {
    this.slideOneForm = this.fb.group({
      transactionId: ['', [Validators.required]],
      customerId: ['', [Validators.required]],
      chequeDepositId: ['', [Validators.required]],
      productCode: ['CQW', [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      accountNumber: ['', [Validators.required]],
      accountBalance: ['', [Validators.required]],
      accountCurrency: ['', [Validators.required]],
      transactionCurrency: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required]],
      branchFlag: [true, [Validators.required]],
      accountBranch: ['', [Validators.required]],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      exchangeRate: [''],
      accountAmount: ['', [Validators.required]],
      totalChargeAmount: ['', [Validators.required]],
      narrative: ['', [Validators.required]],
      denomination: [null, [Validators.required]],
      totalAmount: ['', [Validators.required]],
      createdBy: ['', [Validators.required]],
      createdTime: ['', [Validators.required]],
      modifiedBy: ['', [Validators.required]],
      modifiedTime: ['', [Validators.required]],
      recordStatus: ['', [Validators.required]],
      authStatus: ['', [Validators.required]],
      version: [''],
      remarks: [''],
      totalTransactionAmount: ['', [Validators.required]],
      chequeNumber: ['', [Validators.required, Validators.maxLength(12)]],
    });

  }

  loadData() {
    this.loading.present();
    this.api.custpomerDetails(this.phoneNumber).subscribe((resp) => {
      this.loading.dismiss();
      resp.custAccount = resp.custAccount.filter(card => card.status === "APPROVED");
      this.customerDetails = resp;
      this.upsertAccount(resp);
    }, (err: any) => {
      this.loading.dismiss();
    })
  }

  get f() {
    return this.slideOneForm.controls;
  }

  validateDisablebutton(button) {
    this.slideOneForm.valueChanges.subscribe(v => {
      if (button === 'disable1') {
        if (v.accountBranch != '' && v.accountNumber != '' && v.transactionAmount != ''
          && v.transactionCurrency != '' && v.chequeNumber != '' && v.transactionAmount != 0) {
          this.submitted = false;
        } else {
          this.submitted = true;
        }
      }
      if (button === 'disable2') {
        if (
          v.transactionBranch != '' &&
          v.transactionDate != '' &&
          v.transactionTime != ''
        ) {
          this.submitted1 = false;
        } else {
          this.submitted1 = true;
        }
      }
    })

    if (this.editMode) {
      this.submitted1 = false;
    }
  }


  numberOnlyValidation(event: any) {
    this.transAmt = event.target.value;
    console.log(event.target.value);
    this.IntValue = Math.floor(this.slideOneForm.value.transactionAmount).toString().length;
    if (this.IntValue > 1) {

      let value: string;
      value = this.slideOneForm.value.transactionAmount;
      this.transAmount = value;
      const pattern = value;
      let lastCharIsPoint = false;
      if (pattern.charAt(pattern.length - 1) === '.') {
        lastCharIsPoint = true;
      }
      const num = pattern.replace(/[^0-9.]/g, '');
      this.transAmt = Number(num);
      this.transAmount = this.transAmt.toLocaleString('en-US');
      if (lastCharIsPoint) {
        this.transAmount = this.transAmount.concat('.');
      }

      this.cdr.detectChanges();

    }

    this.transAmt = this.transAmt.toString().replace(/,/g, '');

    if (parseFloat(this.currentBalance) < parseFloat(this.transAmt)) {
      this.openToast1();
    } else {
      return;
    }
  }

  async openToast1() {
    const toast = await this.toastCtrl.create({
      message: 'Transaction Amount should not exceed than Account Balance',
      duration: 2000,
      position: 'bottom'
    });
    toast.present();
  }

  // compareWith(o1, o2) {
  //   return o1 && o2 ? o1.id === o2.id : o1 === o2;
  // }

  selectCurrencyCode(e: any) {
    if (typeof e !== "undefined") {
      const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
      this.selectedCountryCode = filteredCurrency[0].countryCode.toLowerCase();
      this.getExChangeRate(filteredCurrency[0].currencyCode);
    } else {
      return;
    }
  }


  getExChangeRate(currency) {
    this.api.getExchangeRate(this.accountInfo.accountCurrency).subscribe((res: any) => {
      const code = this.currencies.filter((x: any) => x.currencyCode == currency);
      let filteredCurrency = code[0].currencyCode;
      const rate = res?.rates?.[filteredCurrency];
      this.slideOneForm.get('exchangeRate').patchValue(rate);
      console.log("tAmount", this.transAmount);
      if (!isNaN(Number(this.transAmount))) {
        let updatedAmount: number = (Number(this.transAmount) * Number(this.slideOneForm.get('exchangeRate')?.value)) + this.chargeAmount;
        if (!isNaN(Number(updatedAmount))) {
          console.log(Number(updatedAmount));
          let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.slideOneForm.get('transactionCurrency')?.value);
          this.slideOneForm.get('totalTransactionAmount').patchValue(formatedAmount);
          this.cdr.markForCheck();
        }
      }
    });
    this.updateTransAmount();
  }

  updateTransAmount() {
    console.log("Values changing??", this.transAmount);
    let transAmount = this.transAmount;
    transAmount = transAmount.split(',').join('');
    if (!isNaN(Number(transAmount))) {
      let updatedAmount: number = (Number(transAmount) * Number(this.slideOneForm.get('exchangeRate')?.value)) + this.chargeAmount;
      if (!isNaN(Number(updatedAmount))) {
        console.log(Number(updatedAmount));
        let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.slideOneForm.get('transactionCurrency')?.value);
        this.slideOneForm.get('totalTransactionAmount').patchValue(formatedAmount);
        this.cdr.markForCheck();
      }
    }
  }

  upsertAccount(filteredResponseSavingAccount: any) {
    this.users = filteredResponseSavingAccount.custAccount;
    this.customerId = filteredResponseSavingAccount.customerId;
    this.curr = getCurrencySymbol(filteredResponseSavingAccount.custAccount[0].accountCurrency, "narrow");
    this.currentBalance = this.users[0].amount;

    this.selectedCountryCode = this.selectedCurrency.countryCode.toLowerCase();
    this.slideOneForm.get('transactionCurrency')?.patchValue(this.selectedCurrency.currencyCode);

    if (this.editMode) {
      const data = this.routerData?.data;
      this.slideOneForm.get('accountNumber').patchValue(data?.accountNumber);
      this.slideOneForm.get('chequeNumber').patchValue(data?.chequeNumber);
      this.slideOneForm.get('transactionAmount').patchValue(data?.transactionAmount);
      this.slideOneForm.get('remarks').patchValue(data?.remarks);
      this.slideOneForm.get('transactionBranch').patchValue(data?.transactionBranch);
      this.slideOneForm.get('transactionDate').patchValue(data?.transactionDate);
      this.slideOneForm.get('transactionTime').patchValue(data?.transactionTime);
      // this.slideOneForm.get('personName').patchValue(data?.representativeInfo?.[0]?.personName);
      // this.slideOneForm.get('phoneNumber').patchValue(data?.representativeInfo?.[0]?.phoneNo);
      this.slideOneForm.get('totalTransactionAmount').patchValue(data?.totalTransactionAmount);
      this.getChargeDetails();
    } else {
      if (this.accountInfo.accountId != null) {
        this.getChargeDetails();
        this.slideOneForm.get('accountNumber').patchValue(this.accountInfo.accountId);
      } else {
        this.slideOneForm.get('accountNumber').patchValue(this.users[0].accountId);
      }

      this.accountflag = this.users.length == 1 ? true : false;
      this.slideOneForm.controls.accountBranch.patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
      this.slideOneForm.get('transactionBranch').patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
    }
    this.cdr.markForCheck();
  }

  getChargeDetails() {
    this.api.getChargeDetails(this.accountInfo.accountId).subscribe(resp => {
      this.chargeAmount = resp?.[0]?.amount;
      this.slideOneForm.get('totalChargeAmount').patchValue(resp?.[0]?.amount);
    }, err => console.log('Error: ', err))
  }

  previous1() {
    this.router.navigate(['dashboard']);
  }

  goToHomepage() {
    this.router.navigate(['/dashboard']);
  }

  goToNextPage(fb) {
    this.flag = false;

  }

  goToPreviousPage() {
    this.flag = true;
  }

  goToNextScreen() {
    this.api.setIndex({
      index: 'CQW'
    });
    this.router.navigate([
      'token-generation',
      { transactionId: this.transactionId },
    ]);
  }

  save(form) {

    form.transactionDate.toString();

    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.accountCurrency = this.accountDetails?.accountCurrency || "";
    form.customerId = this.customerId;
    form.productCode = this.productCode;
    form.tokenOrigin = this.tokenOrigin;
    form.accountType = this.accountDetails.accountType;

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    this.accountNum = form.accountNumber;
    this.transactionAmount = form.transactionAmount;
    console.log(this.transactionAmount);
    this.transDate = moment(new Date(form.transactionDate)).format("DD-MM-YYYY").toString();

    localStorage.setItem("AccountNumber", this.accountNum);
    localStorage.setItem("TransactionDate", this.transDate);
    localStorage.setItem("TransactionAmount", this.transactionAmount);
    localStorage.setItem("TransactionBranch", form.transactionBranch);


    form.isMobileTrans = true;

    // Total transaction convertion format
    let ttam = this.slideOneForm.value['totalTransactionAmount'].substring(1);
    form.totalTransactionAmount = ttam.replace(/,/g, '');

    if (this.editMode) {
      this.api.appointmentBooking(form).subscribe(resp => {
        this.chequeWithdrawal = resp;
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.transactionId = this.chequeWithdrawal.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (this.chequeWithdrawal === 200 || this.chequeWithdrawal !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.slideOneForm.reset();
          this.onClick(this.chequeWithdrawal);
        }
      })
    } else {
      form.transactionAmount = form.transactionAmount.toString().replace(/,/g, '');
      this.api.cashDepositSave(form).subscribe((resp) => {
        this.chequeWithdrawal = resp;
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.transactionId = this.chequeWithdrawal.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (this.chequeWithdrawal === 200 || this.chequeWithdrawal !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.slideOneForm.reset();
          this.onClick(this.chequeWithdrawal);
        }
      });
    }
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: AppointmentpopupPage,
      componentProps: {
        value: event,
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log("Getting Back Data", this.routerData);
      this.flag = true;
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  format24HrsTo12Hrs(time) {
    var formatted = moment(time, "HH:mm").format("LT");
    return formatted;
  }

  openPopup() {
    console.log("popup");
    this.modalController.create({
      component: TimeSlotsComponent,
      componentProps: {
        date: this.slideOneForm.get('transactionDate').value,
      }
    }).then(modalResp => {
      modalResp.present()
      modalResp.onDidDismiss().then(res => {
        if (res.data != null) {
          console.log(res);
          this.slideOneForm.get('transactionTime').patchValue(res.data);
        }
      })
    })
  }


  accountEvent(e: any) {
    this.isedit = false;
    let data = e?.detail?.value ? e?.detail?.value : e;

    this.api.accountBalance(data).subscribe((accbal: any) => {
      this.accountDetails = accbal;
      this.valueSet(accbal.currentBalance);

      this.currentBalance = accbal.amount;
      this.slideOneForm.controls.accountBalance.patchValue(accbal.amount);
      this.slideOneForm.controls.accountBranch.patchValue(accbal.accountBranch);
      localStorage.setItem("AccBranch", accbal.accountBranch);
      this.slideOneForm.controls.accountCurrency.patchValue(accbal.accountCurrency);

      if (accbal.transactionAmount != null || accbal.transactionAmount != undefined) {
        this.numberOnlyValidation(accbal.transactionAmount);
      }

      if (accbal.lastTransactions != null) {
        if (accbal.lastTransactions.length <= 2) {
          this.slideOneForm.controls.transactionBranch.patchValue(accbal.accountBranch);
        } else {

          var brnCnt = 0;
          var brnOldCnt = 0;
          console.log("Else", accbal.lastTransactions);
          for (var i = 0; i < accbal.lastTransactions.length; i++) {
            if (accbal.lastTransactions[i].transactionBranch != null) {
              for (var n = 0; n < accbal.lastTransactions.length; n++) {
                if (accbal.lastTransactions[n].transactionBranch != null) {
                  if (accbal.lastTransactions[i].transactionBranch === accbal.lastTransactions[n].transactionBranch) {
                    brnCnt = brnCnt + 1;
                  }
                }
              }
            }
            if (brnOldCnt < brnCnt && brnCnt >= 2) {
              this.trnBrn = accbal.lastTransactions[i].transactionBranch;
              brnOldCnt = brnCnt;
              console.log("yhjghguuyjgh");
            }
            brnCnt = 0;
          }
          if (this.trnBrn != null && this.trnBrn !== accbal.accountBranch) {
            console.log(this.trnBrn);
            this.slideOneForm.controls.branchFlag.patchValue(false);
            this.slideOneForm.controls.transactionBranch.patchValue(this.trnBrn);
            console.log(localStorage.getItem("BranchFlag"));
            this.brnflg = localStorage.getItem("BranchFlag");

            this.brnflg = false;
            this.nearestBrn = true;
          } else {
            this.slideOneForm.controls.transactionBranch.patchValue(accbal.accountBranch);
            this.nearestBrn = false;
            this.brnflg = true;
            console.log(this.nearestBrn);
          }
        }
      } else {
        this.slideOneForm.controls.transactionBranch.patchValue(accbal.accountBranch);
        this.nearestBrn = false;
      }
    });
  }


  valueSet(currentBalance) {
    this.currentBalance = currentBalance;
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: "branchModal",
      componentProps: {
      }
    });

    modal.onDidDismiss().then((modelData) => {
      if (modelData !== null) {
        let branch = modelData.data;
        console.log('Modal Data for branch: ', modelData.data);
        this.tokenCount = modelData.data.data.tokenCount;
        this.slideOneForm.get('transactionBranch').patchValue(modelData.data['data'].branchName);
      }
    });

    return await modal.present();
  }

}
